.contacts {
  &__wrapper {
    @include page-grid;
  }

  &__form {
    grid-column: span 2;

    @include on-tablet {
      grid-column: span 3;
    }

    @include on-desktop {
      grid-column: 1 / 5;
    }

    &--form {
      display: flex;
      flex-direction: column;
      gap: 16px;

    }

    &--block {
      height: 48px;
      padding-left: 16px;
      border-radius: 40px;
      background-color: #F8F8FA;
      color: #7E7E83;;
      border: none;

      &-message {
        font-family: Robotto, sans-serif;
        resize: none;
        padding: 14px 16px;
        height: 120px;
        border-radius: 16px;
      }

      &-button {
        color: black;
        margin-top: 16px;
        font-size: 16px;
        font-weight: 700;
        transition: 0.3s;

        &:hover {
          box-shadow: 1px 4px 16px 0px #e8e8f0;
        }
      }
    }
  }

  &__contact {
    grid-column: span 2;

    @include on-tablet {
      grid-column: span 2;
    }

    @include on-desktop {
      grid-column: 8 / 11;
    }

    &_link {
      transition: all 0.3s;

      &:hover {
        @include hover(transform, scale(1.1));
      }
    }
  }
}

textarea, input {
  border: 1px solid transparent;
  outline: none;

  &:focus {
    border-color: black;
  }
}
